body {
    overflow-x: hidden;
    color: #122940;
    line-height: 1.4;
}

.container {
    padding: 0 20px;
    width: 100%;
    max-width: 100%;
}

.bg-orange {
    background-color: #FFAC70;
}

.bg-pink {
    background-color: #FF7979;
}

.bg-green-light {
    background-color: #7FD59C;
}

.bg-gray-light2 {
    background-color: #EDF3F9;
}

@media screen and (min-width: 1024px) {
    .container {
        padding: 0 30px;
    }
}

@media screen and (min-width: 1440px) {
    .container {
        max-width: 1500px;
    }
}

.swiper-custom .swiper {
    padding-bottom: 30px;
}

@media screen and (min-width: 768px) {
    .swiper-custom .swiper {
        padding-bottom: 60px;
    }
}

.swiper-custom .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
}

@media screen and (min-width: 768px) {
    .swiper-custom.md-hide-dots .swiper-pagination {
        display: none;
    }
}

.swiper-custom .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    margin: 0 6px !important;
    background: #D3DCE5;
    opacity: 1;
}

.swiper-custom .swiper-pagination-bullet-active {
    background-color: #122940;
}

@media screen and (max-width: 767px) {
    .swiper-custom .swiper-pagination-bullet-active {
        width: 18px;
        height: 18px;
    }
    .swiper-custom .swiper-pagination-bullet {
        margin: 0 10px !important;
    }
}

.swiper-thumbnail .swiper-slide > div {
    border: 2px solid transparent;
}

.swiper-thumbnail .swiper-slide-thumb-active > div {
    border-color: #122940;
}

.react-tabs .react-tabs__tab-list {
    margin-bottom: 0;
    border-color: #EDF3F9;
}

.react-tabs .react-tabs__tab {
    border-radius: 0;
    bottom: 0;
    border: 0;
    border-bottom: 2px solid transparent;
    padding: 12px 0;
    margin-right: 40px;
}

.react-tabs .react-tabs__tab:last-child {
    margin-right: 0;
}

.react-tabs .react-tabs__tab:focus:after {
    display: none;
}

.react-tabs .react-tabs__tab--selected {
    border-bottom: 2px solid #122940;
}

@media screen and (max-width: 767px) {
    .react-tabs .react-tabs__tab {
        display: block;
    }
    .react-tabs .react-tabs__tab {
        border-bottom: 0;
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance: none;
}

input[type="radio"] + label span,
input[type="checkbox"] + label span{
    transition: background .2s,
    transform .2s;
}

input[type="radio"] + label span:hover,
input[type="radio"] + label:hover span,
input[type="checkbox"] + label span:hover,
input[type="checkbox"] + label:hover span{
    transform: scale(1.2);
}

input[type="radio"]:checked + label span{
    background-color: #122940;
    box-shadow: 0px 0px 0px 3px white inset;
}

input[type="checkbox"]:checked + label span {
    background-color: #122940;
    box-shadow: 0px 0px 0px 4px white inset;
}

input[type="radio"]:checked + label,
input[type="checkbox"]:checked + label{
    color: #122940;
}

.swiper-container {
    width: 480px;
}

@media screen and (min-width: 768px) {
    .swiper-container {
        width: 768px;
    }
}

@media screen and (min-width: 1024px) {
    .swiper-container {
        width: 1024px;
    }
}

/* Hide scrollbar for Chrome, Safari and Opera */
.no-scrollbar::-webkit-scrollbar {
    display: none;
}
/* Hide scrollbar for IE, Edge and Firefox */
.no-scrollbar {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}